import React from 'react';
import "./SearchBar.css";
import { Form, Input, Dropdown} from 'semantic-ui-react';
import axios from "axios";

export class SearchBar extends React.Component {
    state = {
        input:"",
        inputName:"Submit",
        labelName:"SearchBarLabel",
        currentIndex: 1,
        wordIndex: 0,
        lastIndex: [],
        list:[
            {key:0, text: "a"},
            {key:1, text: "s"},
            {key:2, text: "d"},
            {key:3, text: "f"},
            {key:4, text: "g"},
        ]
    }

    onClick= (e)=> {
        console.log("hello");
        e.preventDefault();
       console.log(e.target.value); 
    }

    onChange = async (e) => {
        let currentIndex = this.state.currentIndex;
        let wordIndex = this.state.wordIndex;
        let value = e.target.value;
        let subString = [];
        const length = value.length;
        let lastIndex = this.state.lastIndex;

        if(e.keyCode === 8) {
            currentIndex -= 2;
            if(length - 1 <= wordIndex)
                wordIndex = lastIndex.pop();

        } else if(e.keyCode != 32) {

            if(e.keyCode != 13) {
                subString = value.slice(wordIndex, currentIndex);

                const input = JSON.stringify({autoCorrect: subString});

                await axios.post("https://cyberminerkwic.herokuapp.com/autocorrect", input, 
                    {headers: {
                        "Content-Type": "application/json"
                    }})
                        .then((res) => {
                            if(res) {
                                console.log("res: ", res.data);
                                lastIndex.push(wordIndex);
                                wordIndex = length;
                                const autoCorrect = res.data.autoCorrect
                                console.log("sub: ", subString);
                                value += autoCorrect
                                console.log("value:  ", value);
                            }
                        })
                        .catch((error) => console.log(error));
            }
        } else {
            lastIndex.push(wordIndex);
            wordIndex = length;
        }

        console.log("value currentI", value, currentIndex);  
        console.log(wordIndex);
        console.log(lastIndex);

        this.setState({
            input:value,
            currentIndex: currentIndex  + 1,
            wordIndex: wordIndex,
            lastIndex: lastIndex
        });
    }

    onSubmit = () => {
        console.log(this.state.input);
        if(this.state.input.length) {
            this.setState({
                inputName:"Submitted",
            labelName:"SearchedBarLabel"});
            this.props.onSubmit(this.state.input);
        }
    }

    renderSearchBar = () => {
        return (
            <div className="Form">
                <div className="ui container">
                        <Form className={this.state.inputName} onSubmit={this.onSubmit}>
                            <Form.Field>
                                <label className={this.state.labelName}>CyberMiner</label>
                                <div className="ui container">
                                    <Dropdown
                                        name="input"
                                        placeholder="Search"
                                        search
                                        fluid
                                        options={this.state.list}
                                        value={this.state.value}
                                        //onSearchChange={this.onChange}
                                        //onSubmit={this.onSubmit}
                                        onKeyUp={this.onChange}
                                    />
                                </div>
                            </Form.Field>
                        </Form>
                </div>
            </div>
        );
    }

    render = () => {
        return (
        <div className="SearchBar">
            {this.renderSearchBar()}
        </div>);
    }
}